import "./App.css";
import { slide as Menu } from "@calebegeazi/react-burger-menu";
import CircularProgress from "@mui/material/CircularProgress";
import Box from "@mui/material/Box";
import React, { Suspense, useState } from "react";
import Cookies from "js-cookie";
import { SetResultsFromCookie } from "./util/CookieResultManager";

const Bracket = React.lazy(() => {
  return new Promise((resolve) => {
    setTimeout(() => resolve(import("./components/Bracket/Bracket")), 500);
  });
});
const GroupStage = React.lazy(() =>
  import("./components/GroupStage/GroupStage")
);

const App = () => {
  const result = Cookies.get("result");
  const groupsData = SetResultsFromCookie(result);
  const [loadBracket, setLoadBracket] = useState(
    groupsData.teamsSelected === 16
  );

  return (
    <div className="App text-center bg-slate-200 overflow-hidden">
      <div className="text-xl sm:text-2xl md:text-3xl font-bold p-2 md:p-4 text-slate-600 border-b shadow sticky top-0 z-50 bg-slate-300">
        <Menu>
          <a id="home" className="menu-item" href="/">
            Home
          </a>
          <a id="Stats" className="menu-item" href="/stats">
            Stats
          </a>
          <a
            id="GitHub"
            className="menu-item"
            href="https://github.com/CalebeGeazi/world-cup-2022-bracket/fork"
            target="_blank"
            rel="noreferrer"
          >
            Fork me on Github!
          </a>
        </Menu>
        2022 World Cup Bracket
      </div>
      {loadBracket ? null : (
        <Suspense
          fallback={
            <Box
              sx={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                height: "100vh",
              }}
            >
              <CircularProgress />
            </Box>
          }
        >
          <GroupStage groupsData={groupsData} setLoadBracket={setLoadBracket} />
        </Suspense>
      )}
      {loadBracket ? (
        <Suspense
          fallback={
            <Box
              sx={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                height: "100vh",
              }}
            >
              <CircularProgress />
            </Box>
          }
        >
          <Bracket />
        </Suspense>
      ) : null}
    </div>
  );
};

export default App;
